* {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}

.App {
  display: flex;
}

.birthday {
  border-left: 1px solid rgba(0, 0, 0, 0.2);
}

.birthday__header {
  padding: 1rem;
  font-size: 1.3rem;
  font-weight: 700;
}

.birthday__sections {
  padding: 0 1rem;
}

.birthday__month {
  font-size: 1.2rem;
  font-weight: 700;
  margin: 0.5rem 0;
}

.birthday__item {
  margin: 0.2rem 0;
}

.employees {
  width: 75%;
}

.employees__header {
  padding: 1rem;
  font-size: 1.3rem;
  font-weight: 700;
}

.employees__sections {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-gap: 30px;
  padding: 0 1rem;
}

.employees__letter {
  font-size: 1.2rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
}

.employees__item {
  font-size: 1rem;
  margin-bottom: 20px;
}

.employees__name {
  color: blue;
  font-weight: 700;
}

.employees__item label {
  display: block;
  margin-top: 10px;
}

.employees__item_empty {
  justify-content: center;
}

input {
  cursor: pointer;
}
